.list {
    height: 460px;
    width: 400px;
    background-color: rgba(255, 255, 255, 0.13);
    position: absolute;
    transform: translate(10%, -20%);
    top: 50%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 50px 35px;
    overflow-y: scroll;
}

.btnContlist {
    margin-top: 10px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.btnContlist:hover {
    background-color: #080710;
    color: #ffffff;
}

.info {
    margin-top: 30px;
    font-size: 22px;
    font-weight: 500;
    color: #ffffff;
}

li {
    list-style: none;
}

.headerCont {
    font-size: 42px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
    color: white;
}